/****************************************************************************************/
/* Togo Client */
/****************************************************************************************/
:root {
  font-family: "helvetica neue", helvetica, sans-serif;
  --default-font: "helvetica neue", helvetica, sans-serif;
}

:root {
  font-family: "Sen", "helvetica", times, serif;
}

.c9 dl,
.entry-content p,
.entry-footer {
  line-height: 1.4;
}

.editor-styles-wrapper .block-editor-rich-text__editable a,
.entry-content p a:not(.wp-block-button__link):not(.nav-tabs):not([class|=c9-share]):not(.c9-smooth),
.entry-title a,
.entry-content a {
  color: #121212;
  text-decoration: underline;
  text-underline-offset: 2px;
  text-decoration-thickness: 2px;
  -webkit-text-decoration-style: dotted;
  text-decoration-style: dotted;
}

.entry-title a {
  text-decoration: none;
}

.entry-title a:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
}

.woocommerce-store-notice a,
p.demo_store a {
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
  -webkit-text-decoration-style: dotted;
  text-decoration-style: dotted;
}

.woocommerce-store-notice a:focus,
p.demo_store a:focus,
.woocommerce-store-notice a:hover,
p.demo_store a:hover {
  -webkit-text-decoration-style: solid;
  text-decoration-style: solid;
}

.entry-content a:hover,
a:hover {
  color: #4f4f4f;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
}

.site :focus {
  outline-color: #121212;
}

@media only screen and (max-width: 414px) {
  .entry-content {
    max-width: 100vw;
  }
}
/****************************************************************************************/
/* nav header */
/****************************************************************************************/
@media only screen and (min-width: 992px) {
  .header-navbar .site-branding {
    min-width: 225px;
  }
}
.header-navbar .c9-site-title,
.header-navbar .site-description {
  margin-bottom: 5px;
  text-align: left;
  line-height: 1.2;
}

.header-navbar .c9-site-title a {
  text-decoration: none;
}

.header-navbar .site-branding,
.header-navbar .c9-site-title {
  display: block;
  text-align: left;
}

.header-navbar .navbar {
  background-color: transparent;
}

.header-navbar .nav .nav-item .dropdown-item,
.header-navbar .nav .nav-item .nav-link,
.header-navbar .nav-item,
.header-navbar .search {
  line-height: 1;
  height: 44px;
}

.header-navbar .dropdown > .dropdown-toggle {
  margin-top: -2px;
}

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search,
.navbar-light .navbar-nav .nav-link {
  color: #000000;
}

.header-navbar .navbar-toggler:focus,
.header-navbar .nav-search .btn-nav-search:focus {
  color: #000000;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
  background-color: #ffffff;
}

.header-navbar .justify-content-center {
  justify-content: flex-end !important;
}

.dropdown-toggle::after {
  border-right: none;
  border-left: none;
}

.header-navbar .nav .search .nav-link .fa-search,
.header-navbar .nav .nav-link .fa-shopping-cart,
.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search {
  font-size: 1.4rem;
}

.header-navbar .navbar .nav .dropdown-toggle::after {
  content: " \f0d7";
  font-family: FontAwesome;
  margin: 0px;
  padding: 1px 0px 0px 2px;
  border: none;
  width: auto;
  height: 14px;
  vertical-align: 0;
  font-size: 1.4rem;
}

.header-navbar .nav-item {
  margin: 0px 14px;
}

.header-navbar .nav-item .dropdown-menu .nav-item {
  margin: 0px 6px;
}

.header-navbar .nav-item .nav-highlight {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: #000000;
  height: 4px;
  transition: right 0.5s;
}

.header-navbar .nav-item > .nav-link:hover > .nav-highlight,
.header-navbar .nav-item > .nav-link:focus > .nav-highlight,
.header-navbar .nav-item:focus > .nav-link > .nav-highlight {
  right: 0% !important;
}

.header-navbar .navbar .nav .search .nav-search a {
  font-size: 1.4rem;
}

.header-navbar .dropdown-item,
.header-navbar .nav-link {
  color: #000000;
  font-size: 1.4rem;
  font-family: inherit;
  text-transform: none;
  position: relative;
  letter-spacing: 0.8px;
  padding: 14px 0px;
}

.header-navbar .navbar .nav .nav-item .dropdown-item {
  padding: 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item .nav-link {
  padding: 14px 8px;
}

.header-navbar .nav .nav-item .dropdown-menu {
  background-color: #00d082;
  border-radius: 0px;
  margin-top: 38px;
  border: 0px;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .navbar-small .nav-item .dropdown-menu {
  margin-top: 5px;
  position: absolute;
}

@media only screen and (max-width: 991px) {
  .header-navbar .nav-item.show {
    height: auto;
  }
  .header-navbar .nav .nav-item .dropdown-menu {
    margin-top: 0px;
  }
  .header-navbar .nav .nav-item .dropdown-menu {
    min-width: 100%;
  }
}
.header-navbar .nav .nav-item .dropdown-menu.show {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav-order {
  display: inline-block;
  font-size: 2rem;
}

.header-navbar .nav-order:empty {
  display: none !important;
}

.header-navbar .navbar-light .navbar-toggler {
  margin-right: 0;
}

.header-navbar .nav-order .nav-link,
.header-navbar .dropdown-item:focus,
.header-navbar .dropdown-item:hover {
  text-decoration: none;
  color: #121212;
}

@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
.nav-woocommerce:hover a:before,
.nav-woocommerce:hover a .nav-highlight,
.nav-link .view-cart {
  display: none;
}

@media only screen and (max-width: 991px) {
  .nav-search,
  .nav-toggle,
  .nav-order {
    display: inline-block;
    width: 40px;
  }
  .navbar-toggler[aria-expanded=true] .fa-bars:before {
    content: "\f00d";
  }
  .nav-link .view-cart {
    display: inline;
  }
}
/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/
.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
  background-color: #000000;
  color: #ffffff;
}

.header-navbar .nav .nav-item .nav-link {
  transition: color 0.3s ease-in-out;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-order-now.active > .nav-link,
.c9 .header-navbar .navbar-nav .nav-item.c9-order-now > .nav-link,
.is-style-c9-btn-green .wp-block-button__link {
  padding-left: 14px;
  padding-right: 14px;
  color: #ffffff;
  background: linear-gradient(151deg, rgb(122, 220, 180) 0%, rgb(0, 208, 130) 100%);
  border-radius: 34px;
}

.header-navbar .navbar .nav > .nav-item.active.c9-order-now > .nav-link span,
.header-navbar .navbar .nav > .nav-item.nav-highlight.c9-order-now > .nav-link span,
.header-navbar .navbar .nav > .nav-item.c9-order-now:hover > .nav-link span {
  display: none;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-order-now .nav-link:hover,
.c9 .header-navbar.navbar-small .navbar-nav .nav-item.c9-order-now .nav-link:hover,
.is-style-c9-btn-green:hover .wp-block-button__link {
  background: linear-gradient(343deg, rgb(255, 230, 5) 0%, rgb(242, 255, 3) 100%);
  color: #000000 !important;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-yellow-btn > .nav-link {
  padding-left: 14px;
  padding-right: 14px;
  border-radius: 34px;
  background: linear-gradient(343deg, rgb(255, 230, 5) 0%, rgb(242, 255, 3) 100%);
  color: #000000;
}

.header-navbar .navbar .nav > .nav-item.active.c9-yellow-btn > .nav-link span,
.header-navbar .navbar .nav > .nav-item.nav-highlight.c9-yellow-btn > .nav-link span,
.header-navbar .navbar .nav > .nav-item.c9-yellow-btn:hover > .nav-link span {
  display: none;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-yellow-btn .nav-link:hover,
.c9 .header-navbar.navbar-small .navbar-nav .nav-item.c9-yellow-btn .nav-link:hover {
  color: #ffffff;
  background: linear-gradient(151deg, rgb(122, 220, 180) 0%, rgb(0, 208, 130) 100%);
}

.header-navbar .navbar.navbar-small .nav .nav-item.active:not(.c9-order-now) > .nav-link {
  color: #000000;
}

.header-navbar .navbar:not(.navbar-small) .nav-link:hover,
.header-navbar .dropdown-item:hover,
.header-navbar .navbar-toggler:hover,
.header-navbar .nav-link:hover,
.header-navbar .nav > .nav-item > .nav-link:focus,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-woocommerce .nav-link:hover,
.header-navbar .nav-woocommerce .nav-link:focus,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: #00d082;
}

.header-navbar .navbar.navbar-small .nav .nav-item.active > .nav-link:hover {
  color: #00d082;
}

/****************************************************************************************/
/* mobile navigation background*/
/****************************************************************************************/
@media only screen and (max-width: 667px) {
  .header-navbar .navbar {
    background-color: #ffffff;
  }
}
@media only screen and (max-width: 991px) {
  .header-navbar .navbar-collapse,
  .header-navbar .navbar-collapse.show {
    background-color: #ffffff;
    left: 0;
  }
}
