/****************************************************************************************/
/* post navigation, tags, categories, on single posts */
/****************************************************************************************/
.single .navigation .nav-next a,
.single .navigation .nav-previous a {
  font-family: inherit;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
}

.single .navigation .nav-next a:hover,
.single .navigation .nav-previous a:hover {
  color: #00d082;
}

@media only screen and (min-width: 768px) {
  .single .entry-footer {
    padding: 0px;
  }
}
.entry-footer .cat-links {
  font-size: 1.4rem;
}

.entry-footer .cat-links,
.entry-footer .tag-links {
  margin-bottom: 20px;
  display: block;
}

/* tags */
.i-tag,
.tags-links,
.tags-links a {
  font-weight: 900;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
  color: #4a4a4a;
